import React from "react"
import { Link } from "gatsby"
import { graphql, StaticQuery } from "gatsby"
import Layout from "../components/layout"
import SEO from "../components/seo"
import "../utils/normalize.css"
import "../utils/css/screen.css"
import { NameCard } from "../components/css_practice"

const CSSProjects = ({ data }) => {
  const siteTitle = data.site.siteMetadata.title

  return (
    <Layout title={siteTitle}>
      <SEO title="ABout" keywords={[`blog`, `gatsby`, `javascript`, `react`]} />
      <section className="post-content">
        <header className="post-content-header">
          <h1 className="post-content-title">HTML X CSS 練習紀錄</h1>
        </header>
        <div className="post-content-body">
          <h4>01. 名片設計</h4>
          <div className="post-content-projects-tags">
            <code>HTML</code>
            <code>CSS</code>
          </div>
          <h6>box-shadow 陰影</h6>
          <p>一般來實現元素的陰影效果，會透過 box-shadow 來設置，有多個參數值，可以分別填入 x 軸偏移量、 y 軸偏移量、模糊值（邊緣是否清晰）、擴散（陰影放大縮小）和顏色，其中 y 值為正時，元素是往下位移的。</p>
          <pre className="post-content-projects">
            <code>box-shadow: 0px 25px 15px -20px rgba(0, 0, 0, 0.2);</code>
          </pre>
          <NameCard />
        </div>
      </section>
    </Layout>
  )
}

const indexQuery = graphql`
  query {
    site {
      siteMetadata {
        title
      }
    }
    benchAccounting: file(
      relativePath: { eq: "bench-accounting-49909-unsplash.jpg" }
    ) {
      childImageSharp {
        fluid(maxWidth: 1360) {
          ...GatsbyImageSharpFluid
        }
      }
    }
  }
`

export default props => (
  <StaticQuery
    query={indexQuery}
    render={data => (
      <CSSProjects location={props.location} data={data} {...props} />
    )}
  />
)
